import * as _ from 'lodash';
import * as L from './libs/leaflet';
import WorldmapCtrl, { Data, DataPoint } from './worldmap_ctrl';

const DEFAULT_TILE_SERVERS = {
  'CartoDB Positron': {
    url: 'https://cartodb-basemaps-{s}.global.ssl.fastly.net/light_all/{z}/{x}/{y}.png',
    attribution:
      '&copy; <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a> ' +
      '&copy; <a href="http://cartodb.com/attributions">CartoDB</a>',
    subdomains: 'abcd',
  },
  'CartoDB Dark': {
    url: 'https://cartodb-basemaps-{s}.global.ssl.fastly.net/dark_all/{z}/{x}/{y}.png',
    attribution:
      '&copy; <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a> ' +
      '&copy; <a href="http://cartodb.com/attributions">CartoDB</a>',
    subdomains: 'abcd',
  },
};

function isHighDensity() {
  return ((window.matchMedia && (window.matchMedia('only screen and (min-resolution: 124dpi), only screen and (min-resolution: 1.3dppx), only screen and (min-resolution: 48.8dpcm)').matches || window.matchMedia('only screen and (-webkit-min-device-pixel-ratio: 1.3), only screen and (-o-min-device-pixel-ratio: 2.6/2), only screen and (min--moz-device-pixel-ratio: 1.3), only screen and (min-device-pixel-ratio: 1.3)').matches)) || (window.devicePixelRatio && window.devicePixelRatio > 1.3));
}
type Marker = {
  location: string,
  circle: L.CircleMarker,
  tooltip?: L.Tooltip,
}
export default class WorldMap {
  ctrl: WorldmapCtrl;
  mapContainer: any;
  markers: Marker[];
  map: any;
  legend: any;
  circlesLayer: any;
  tileServers: any;

  constructor(ctrl, mapContainer, tileServers) {
    this.ctrl = ctrl;
    this.mapContainer = mapContainer;
    this.markers = [];
    this.tileServers = tileServers;
  }

  createMap() {
    const mapCenter = (<any>window).L.latLng(
      parseFloat(this.ctrl.panel.mapCenterLatitude),
      parseFloat(this.ctrl.panel.mapCenterLongitude)
    );
    this.map = L.map(this.mapContainer, {
      worldCopyJump: true,
      preferCanvas: true,
      center: mapCenter,
      zoom: parseInt(this.ctrl.panel.initialZoom, 10) || 1,
      maxZoom: parseInt(this.ctrl.panel.maxZoom, 10) || 17,
      minZoom: parseInt(this.ctrl.panel.minZoom, 10) || 0,
    });
    this.setMouseWheelZoom();

    const availableTileServers = _.defaults({}, DEFAULT_TILE_SERVERS, this.tileServers);
    const selectedTileServer = availableTileServers[this.ctrl.tileServer];

    let params = selectedTileServer.params || {};
    if (isHighDensity()) {
      params = { ...params, ...(selectedTileServer.hdpiParams || {}) };
    }

    (<any>window).L.tileLayer(selectedTileServer.url, {
      maxZoom: 18,
      subdomains: selectedTileServer.subdomains,
      reuseTiles: true,
      detectRetina: true,
      attribution: selectedTileServer.attribution,
      ...params
    }).addTo(this.map);
  }

  createLegend() {
    this.legend = (<any>window).L.control({ position: 'bottomleft' });
    this.legend.onAdd = () => {
      this.legend._div = (<any>window).L.DomUtil.create('div', 'info legend');
      this.legend.update();
      return this.legend._div;
    };

    this.legend.update = () => {
      const thresholds = this.ctrl.data.thresholds;
      let legendHtml = '';
      legendHtml +=
        '<div class="legend-item"><i style="background:' +
        this.ctrl.panel.colors[0] +
        '"></i> ' +
        '&lt; ' +
        thresholds[0] +
        '</div>';
      for (let index = 0; index < thresholds.length; index += 1) {
        legendHtml +=
          '<div class="legend-item"><i style="background:' +
          this.ctrl.panel.colors[index + 1] +
          '"></i> ' +
          thresholds[index] +
          (thresholds[index + 1] ? '&ndash;' + thresholds[index + 1] + '</div>' : '+');
      }
      this.legend._div.innerHTML = legendHtml;
    };
    this.legend.addTo(this.map);
  }

  needToRedrawCircles(data: Data) {
    if (this.markers.length === 0 && data.length > 0) {
      return true;
    }

    if (this.markers.length !== data.length) {
      return true;
    }

    const locations = this.markers.map(m => m.location).sort();
    const dataPoints = _.map(data, 'key').sort();
    return !_.isEqual(locations, dataPoints);
  }

  filterEmptyAndZeroValues(data: Data): Data {
    return _.filter(data, o => {
      return !(this.ctrl.panel.hideEmpty && _.isNil(o.value)) && !(this.ctrl.panel.hideZero && o.value === 0);
    }) as Data;
  }

  clearCircles() {
    if (this.circlesLayer) {
      this.circlesLayer.clearLayers();
      this.removeCircles();
      this.markers = [];
    }
  }

  drawCircles() {
    const data = this.filterEmptyAndZeroValues(this.ctrl.data);
    if (this.needToRedrawCircles(data)) {
      this.clearCircles();
      this.createCircles(data);
    } else {
      this.updateMarkers(data);
    }
  }

  createCircles(data: Data) {
    const markers: Marker[] = [];
    data.forEach(dataPoint => {
      if (!dataPoint.locationName) {
        return;
      }
      markers.push(this.createMarker(dataPoint));
    });
    this.circlesLayer = this.addCircles(markers.map(m => m.circle));
    this.markers = markers;
  }

  updateMarkers(data: Data) {
    data.forEach(dataPoint => {
      if (!dataPoint.locationName) {
        return;
      }

      const marker = _.find(this.markers, cir => {
        return cir.location === dataPoint.key;
      });
      const circle = (marker as any).circle;
      if (circle) {
        circle.setRadius(this.calcCircleSize(dataPoint.value || 0));
        circle.setStyle({
          color: this.getColor(dataPoint.value),
          fillColor: this.getColor(dataPoint.value),
          fillOpacity: 0.5,
          location: dataPoint.key,
        } as any);
        circle.unbindPopup();
        this.createPopup(circle, dataPoint.locationName, dataPoint.valueRounded);
      }
      const tooltip = (marker as any).tooltip;
      if (tooltip) {
        const label = this.getTooltipContents(dataPoint);
        tooltip.setTooltipContent(label);
      }
    });
  }

  createMarker(dataPoint: DataPoint): Marker {
    const circle = ((<any>window).L).circleMarker([dataPoint.locationLatitude, dataPoint.locationLongitude], {
      radius: this.calcCircleSize(dataPoint.value || 0),
      color: this.getColor(dataPoint.value),
      fillColor: this.getColor(dataPoint.value),
      fillOpacity: 0.5,
      location: dataPoint.key,
    }) as L.CircleMarker;
    let tooltip: L.Tooltip | undefined = undefined
    if (this.ctrl.panel.tooltip) {
      const label = this.getTooltipContents(dataPoint);
      tooltip = circle.bindTooltip(label, {
        permanent: true,
        direction: "center",
        opacity: 1,
        className: "value-tooltip",
      }).openTooltip().getTooltip();
    }
    this.createPopup(circle, dataPoint.locationName, dataPoint.valueRounded);

    return { circle, tooltip, location: dataPoint.key };
  }
  getTooltipContents(dataPoint: DataPoint) {
    const { valueRounded: value } = dataPoint;
    if ((this.ctrl.panel.tooltip || {}).showUnit) {
      const unit: string = value && value === 1 ? this.ctrl.panel.unitSingular : this.ctrl.panel.unitPlural;
      return (value + ' ' + (unit || '')).trim();
    }
    return `${value}`.trim();
  }
  calcCircleSize(dataPointValue: number) {
    const circleMinSize = parseInt(this.ctrl.panel.circleMinSize, 10) || 2;
    const circleMaxSize = parseInt(this.ctrl.panel.circleMaxSize, 10) || 30;

    if (this.ctrl.data.valueRange === 0) {
      return circleMaxSize;
    }

    const dataFactor = (dataPointValue - this.ctrl.data.lowestValue) / this.ctrl.data.valueRange;
    const circleSizeRange = circleMaxSize - circleMinSize;

    return circleSizeRange * dataFactor + circleMinSize;
  }

  createPopup(circle: L.CircleMarker, locationName: string | undefined, value: number) {
    const unit: string = value && value === 1 ? this.ctrl.panel.unitSingular : this.ctrl.panel.unitPlural;
    const label = (locationName + ': ' + value + ' ' + (unit || '')).trim();
    circle.bindPopup(label, {
      offset: (<any>window).L.point(0, -2),
      className: 'worldmap-popup',
      closeButton: this.ctrl.panel.stickyLabels,
    });

    circle.on('mouseover', function onMouseOver(evt) {
      const layer = evt.target;
      layer.bringToFront();
      this.openPopup();
    });

    if (!this.ctrl.panel.stickyLabels) {
      circle.on('mouseout', function onMouseOut() {
        circle.closePopup();
      });
    }
  }

  getColor(value: number) {
    for (let index = this.ctrl.data.thresholds.length; index > 0; index -= 1) {
      if (value >= this.ctrl.data.thresholds[index - 1]) {
        return this.ctrl.panel.colors[index];
      }
    }
    return _.first(this.ctrl.panel.colors);
  }

  resize() {
    this.map.invalidateSize();
  }

  panToMapCenter() {
    this.map.panTo([parseFloat(this.ctrl.panel.mapCenterLatitude), parseFloat(this.ctrl.panel.mapCenterLongitude)]);
    this.ctrl.mapCenterMoved = false;
  }

  removeLegend() {
    this.legend.remove(this.map);
    this.legend = null;
  }

  setMouseWheelZoom() {
    if (!this.ctrl.panel.mouseWheelZoom) {
      this.map.scrollWheelZoom.disable();
    } else {
      this.map.scrollWheelZoom.enable();
    }
  }

  addCircles(circles: L.CircleMarker[]) {
    return (<any>window).L.layerGroup(circles).addTo(this.map);
  }

  removeCircles() {
    this.map.removeLayer(this.circlesLayer);
  }

  setZoom(zoomFactor) {
    this.map.setZoom(parseInt(zoomFactor, 10));
  }

  setMinZoom(minZoom) {
    this.map.setMinZoom(parseInt(minZoom, 10));
  }

  setMaxZoom(maxZoom) {
    this.map.setMaxZoom(parseInt(maxZoom, 10));
  }

  remove() {
    this.markers = [];
    if (this.circlesLayer) {
      this.removeCircles();
    }
    if (this.legend) {
      this.removeLegend();
    }
    this.map.remove();
  }
}
